.grid{
    width:100%;
    overflow-x: hidden;
    position: relative;
    display: grid;
    grid-template-areas: "calender todo notes" "dairy events notes";
}
.grid section{
    display: flex;
    justify-content:flex-start;
    flex-direction: column;
    position: relative;
    padding:10px;
}
.calender,.todo,.dairy,.events{
    height:50vh;
    min-height:300px;
}
.notes{
    height:100vh;
    max-height:500px;
}
.calender{
    width:calc(100vw / 3);
    grid-area: calender;
}

.notes{
    width:calc(100vw / 3);
    grid-area: notes;
}

.events{
    width:calc(100vw / 3);
    grid-area: events;
}

.todo{
    width:calc(100vw / 3);
    grid-area: todo;
}
.todo .addTodo p{
    font-size:20px;
    color:#4ea6b8;
}
.todo .addTodo div{
    margin:10px 0;
}
.todo .addTodo div input{
    font-size:13px;
    outline:none;
    border:none;
    background-color: whitesmoke;
    padding:10px;
    border-radius:5px;
    display: inline-block;
    width:calc(100% - 100px);
    max-width:250px;
}
.todo .addTodo div button{
    font-size:18px;
    display: inline-block;
    margin-left:5px;
    transition:0.3s;
    width:30px;
    height:30px;
    text-align: center;
    line-height:25px;
    border:none;
    outline: none;
    border-radius:50%;
    background-color: #4ea6b8;
    color: white;
    font-weight:bold;
    cursor: pointer;
}
.todo .noTodo{
    width:100%;
    margin-top:50px;
    font-size:20px;
    font-weight:bold;
    letter-spacing: 1px;
    opacity:0.5;
    margin-bottom:20px;
}
.todo .todoList{
    position: relative;
    width:100%;
    min-height:200px;
    overflow-x: hidden;
    overflow-y: auto;
}
.todo .todoList .todoItem{
    width:100%;
    margin:10px 0;
    padding:5px 10px 5px 0px;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.todo .todoList .todoItem div{
    margin-right:10px;
}
.todo .todoList .todoItem div i{
    cursor: pointer;
    font-size:20px;
}
.todo .todoList .todoItem div i:nth-child(2),
.todo .todoList .todoItem div i:nth-child(3){
    display:none;
    color:red;
    margin:0 20px;
}
.todo .todoList .todoItem div i:nth-child(2){
    color: #4ea6b8;
}
.todo .todoList .todoItem.openOptions div i:nth-child(2),
.todo .todoList .todoItem.openOptions div i:nth-child(3){
    display:inline;
}
.todo .todoList .todoItem div i.options{
    opacity:0.5;
}
.todo .todoList .todoItem p{
    font-size:15px;
    width:calc(100% - 50px);
}
.todo .todoList .todoItem.openOptions p{
    font-size:15px;
    width:calc(100% - 150px);
}
.todo .todoList .todoItem.done p{
    opacity:0.4;
    text-decoration:line-through;
}


.dairy{
    width:calc(100vw / 3);
    height:50vh;
    grid-area: dairy;
}

@media only screen and (max-width: 1000px){
    .grid{
        grid-template-areas: "calender todo" "dairy events" "notes notes";
    }
    .calender,.dairy,.events,.todo{
        width:50vw;
    }
    .notes{
        width:100vw;
        height:50vh;
    }
}


@media only screen and (max-width: 700px){
    .grid{
        grid-template-areas: "calender" "dairy" "todo" "events" "notes";
    }
    .calender,.dairy,.events,.todo,.notes{
        width:100vw;
        height:50vh;
        padding-left:25px;
        padding-right:10px;
    }
}